import {useState, useEffect} from 'react'
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";
import { getArtistsDataById } from './api';
import { EditionCard } from './components/EditionCard'
import { FormattedMessage } from 'react-intl';

const ArtistDetail: React.FC = () => {

    const params = new URLSearchParams(window.location.search);
    const artistId: string | null = params.get("id");
    
    const [collectionData, setCollectionData] = useState<Array<any>>([])
    const [loading, setLoading] = useState<boolean>(true)
    const [artistName, setArtistName] = useState<string>('')
    const [aboutArtist, setAboutArtist] = useState<string>('')
    const [artistImageName, setArtistImageName] = useState<any>('')
    const [artistCoverImageName, setArtistCoverImageName] = useState<any>('')
    const [primarySaleAmount, setPrimarySaleAmount] = useState<any>(null)


    useEffect(() => {
        getArtistsDataById(artistId)
            .then((response) => {
                if(response.data.success && response.data.artist_data) {
                    setArtistName(response.data.artist_data.artist_name)
                    setArtistImageName(response.data.artist_data.artist_image)
                    setArtistCoverImageName(response.data.artist_data.artist_cover_image)
                    setAboutArtist(response.data.artist_data.about_artist)
                    setCollectionData(response.data.collection_data)
                    setPrimarySaleAmount(response.data.primary_sale_amount)

                    setLoading(false)
                }
            })
    }, [])

    return (
        <>
        {!loading &&
            <>
            <div className="bg-white" style={{ marginTop: '-30px' }}>
                <div className="position-relative m-auto w-100 h-300px">
                    <img 
                        style={{width: 'inherit', height: 'inherit'}} 
                        src={
                            `${process.env.REACT_APP_API_ARTIST_COVER_IMAGE_URL}/${artistCoverImageName}`
                        }
                        alt=""
                    />
                </div>
                <div className="d-flex m-auto w-100" style={{height: '130px'}}>
                    <div className="d-flex pb-0">
                        <div 
                            className="position-relative profile-position" 
                            style={{ width: '180px', height: '180px', borderRadius: '50%', marginLeft: '30px' }}
                        >
                            <img 
                                className="position-absolute" 
                                style={{ width: '180px', height: '180px', borderRadius: '50%' }}
                                // src="https://www.meme-arsenal.com/memes/100773de10bd652a2366e129c5053a0a.jpg" 
                                src={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${artistImageName}`}
                                alt=""
                            />
                        </div>
                    </div>
                    <div className='d-flex justify-content-between flex-wrap w-100'>
                        <div className="d-flex flex-column ms-3 mt-4">
                            <span className="fw-bolder" style={{ fontSize: '30px' }}>{artistName}</span>
                            <span className="text-muted" style={{ fontSize: '20px' }}><FormattedMessage id="ARTIST_DETAIL.DROPS" /> : 
                            <span className='ms-3'>
                                {collectionData.length}
                            </span>
                            </span>
                            <span className="text-muted" style={{ fontSize: '20px' }}><FormattedMessage id="ARTIST_DETAIL.PRIMARY_SALE" /> : 
                            <span className='ms-3'>
                                {Number(primarySaleAmount).toFixed(2)} USD
                            </span>
                            </span>
                        </div>
                    </div>
                </div>
                <hr className="mx-auto" style={{ width: '96%' }} />
            </div>
            <div className='row g-5 g-xl-8 mx-5'>
                <div className='col-xl-4'>
                    <div className={`card`}>
                        <div className='card-header border-0 pt-5'>
                            <h3 className='card-title align-items-start flex-column'>
                                <span className='card-label fw-bolder text-dark'><FormattedMessage id="CREATE_EDIT_ARTIST.ABOUT_ARTIST" /></span>
                            </h3>
                        </div>

                        <div className='card-body pt-5'>
                            {JSON.parse(aboutArtist.replace(/&quot;/g, '"')).map((lines: any) => (
                                <>
                                    <span className='mb-2'>{lines}</span> <br />
                                </>
                            ))}
                        </div>
                        {/* end::Body */}
                    </div>
                </div>
                <div className='col-xl-8'>
                    <div 
                        className='stepper-item mb-5' 
                        style={{ borderBottom: '3px solid #009EF7', width: '80px' }}
                    >
                        <h3 className='stepper-title'><FormattedMessage id="ARTIST_DETAIL.EDITIONS" /></h3>
                    </div>
                    {collectionData.length > 0 && 
                        <>
                            {collectionData.map((data) => (
                                <EditionCard 
                                    collectionID={data.collection_id}
                                    collectionCoverImagePath={`${process.env.REACT_APP_API_COLLECTION_COVER_IMAGE_URL}/${data.collection_cover_image}`}
                                    collectionName={data.collection_name}
                                    collectionType={data.collection_type}
                                    artistName={artistName}
                                    artistImagePath={`${process.env.REACT_APP_API_ARTIST_IMAGE_URL}/${artistImageName}`}
                                    totalTokens={data.total_quantity}
                                    totalOwnership={data.total_ownership}
                                    primarySale={data.primary_sale}
                                />
                            ))}
                        </>
                    }
                    {collectionData.length == 0 && 
                        <div className={`card`}>

                            <div className='card-body text-center pt-5'>
                                <span className='text-center'><FormattedMessage id="ARTIST_DETAIL.NO_DROPS" /></span>
                            </div>
                            {/* end::Body */}
                        </div>
                    }
                    
                </div>
            </div>  
            </>
        }
        {loading && 
            <div className='d-flex w-100 h-100'>
                <div className='my-auto mx-auto'>
                    <div className='d-flex flex-column justify-content-center'>
                        <span className='text-primary fs-3'>Loading</span> <br />
                        <span className='ms-4'>
                            <CircularProgress />
                        </span>
                    </div>
                </div>
            </div>
        }
        </>
    )
}

export default ArtistDetail
