import { FC } from 'react'
import { FormattedMessage } from 'react-intl'

type Props = {
    collectionID: string
    collectionCoverImagePath: string
    collectionName: string
    collectionType: string
    artistName: string
    artistImagePath: string
    totalTokens: string
    totalOwnership: string
    primarySale: any
}

export const EditionCard: FC<Props> = ({
    collectionID,
    collectionCoverImagePath,
    collectionName,
    collectionType,
    artistName,
    artistImagePath,
    totalTokens,
    totalOwnership,
    primarySale
}) => {

    console.log(primarySale)

  return (
      <>
            <div 
                className="card mb-7"
                style={{ 
                    borderRadius: '5px'
                }}
            >
                <a 
                    href={`drop-detail?id=${collectionID}`}
                    style={{ padding: '0px' }}
                    className="card-body d-flex flex-column bg-white rounded"
                >
                    <div className="text-center position-relative w-100">
                        <img 
                            src={collectionCoverImagePath} 
                            className="w-100" 
                            style={{ 
                                borderTopLeftRadius: '5px',
                                borderTopRightRadius: '5px',
                                textAlign: 'center',
                                height: '500px'
                            }}
                        />
                        {/* <div 
                            className="position-absolute d-flex flex-column"
                            style={{ top: '50%', left: '47%', color: '#ffff'}}
                        >
                            <span className='fs-4'>{collectionType}</span>
                            <span className='fs-1'>{collectionName}</span>
                        </div> */}
                    </div>
                    <div className="d-flex justify-content-between px-4 py-4">
                        <div className="d-flex justify-content-start">
                            <div>
                                <img 
                                    className="w-50px"
                                    style={{ borderRadius: '50%' }}
                                    src={artistImagePath} 
                                    alt="" 
                                />
                            </div>
                            <div className="mt-2 ms-3">
                                <span style={{ color: 'black' }}>{collectionName}</span><br/>
                                <span className="text-muted">{artistName}</span>
                            </div>
                        </div>

                        <div className="d-flex justify-content-between">
                            <div className="d-flex flex-column">
                                <span className='fs-4 fs-bold text-muted'><FormattedMessage id="ARTIST_DETAIL.TOTAL_TOKENS" /></span>
                                <span className='fs-5 text-dark'>{totalTokens}</span>
                            </div>
                            <div className="d-flex flex-column mx-7">
                                <span className='fs-4 fs-bold text-muted'><FormattedMessage id="ARTIST_DETAIL.TOTAL_OWNERSHIP" /></span>
                                <span className='fs-5 text-dark'>{parseFloat(totalOwnership)} %</span>
                            </div>
                            <div className="d-flex flex-column">
                                <span className='fs-4 fw-bold text-muted'><FormattedMessage id="ARTIST_DETAIL.PRIMARY_SALE" /></span>
                                <span className='fs-5 text-dark'>
                                    {Number(parseFloat(primarySale).toFixed(5))} 
                                    <span className='ms-2'>USD</span>
                                </span>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
      </>
  )
}
 